@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&family=Roboto&display=swap);
html {
  scroll-behavior: smooth;
  overflow-y: scroll;
  height: 100%;
}

html,
body {
  font-size: 16px;
  min-height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif !important;
  background-color: #1d1e2800;
  background-image: url(/static/media/bg.c92b579d.png);
  background-position: center center;
  background-attachment: fixed;
  background-size: cover !important;
  color: white !important;
}

#root {
  display: flex;
  flex: 1 0 auto;
  min-height: 100%;
  overflow-y: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif !important;
  font-weight: 700 !important;
}

.floating {
  animation-name: floating;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  margin-left: 30px;
  margin-top: 5px;
  transform: translate(0, 10px);
}

@keyframes floating {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, 15px);
  }
  100% {
    transform: translate(0, -0px);
  }
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 0px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #7289da;
  border-radius: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #4e5d94;
}

::selection {
  background-color: #1d1e28 !important;
}

#main-content {
  transition: all 200ms;
  height: 100%;
}

#main-container {
  background-image: url(/static/media/blurple-waves.b85687f6.svg);
  background-attachment: fixed;
  background-position: bottom;
  background-size: 200%;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
}

#main-container > nav {
  margin-left: -15px;
  margin-right: -15px;
  margin-bottom: 0;
  box-shadow: none;
}

.btn {
  font-family: "Poppins", sans-serif !important;
  font-weight: 700 !important;
  border-radius: 10px !important;
  font-size: 16px !important;
  font-weight: 600 !important;
  text-transform: uppercase !important;
  outline: none;
  border: none;
  margin: 0;
  margin-bottom: 0.75rem;
}

.btn {
  margin-right: 0.5em;
}

.btn:hover {
  background-color: #5667a6;
}

.btn-lg {
  font-size: 1em;
  border-radius: 0.25rem;
  padding: 15px 48px;
}

.btn-primary {
  background-color: #7289da;
}

.btn-secondary {
  background-color: #2c2f33;
}

.btn-premium {
  background-color: #3b7bbf;
}

.btn-premium:hover {
  background-color: #2f6298;
}

