.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 0;
  transition: opacity 250ms ease-in;
}

.fade-enter-done {
  transition: opacity 250ms ease-in;
  opacity: 1;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 250ms ease-in;
}

.fade-exit-done {
  transition: opacity 250ms ease-in;
  opacity: 0;
}

.navbar {
  font-family: "Poppins", sans-serif !important;
  font-weight: 700 !important;
}

.navbar-dark .navbar-toggler-icon {
  background-image: url(/static/media/menu_icon.eecba858.svg) !important;
  font-size: 0.75em;
}

.navbar-dark .navbar-nav .nav-link {
  color: white;
  transition: color 200ms;
}

.navbar-collapse {
  padding: 0.25rem 0.75rem;
}

.navbar .navbar-toggler {
  outline: 0;
  cursor: pointer;
  border: none;
}

