.sub-card {
  color: rgba(255, 255, 255, 0.863) !important;
  font-size: 17px;
  border-radius: 20px;
  border-width: 4px;
  background-color: rgba(29, 30, 40, 0.835) !important;
}

.sub-card .card-title {
  font-family: "Poppins", sans-serif !important;
  font-weight: 700 !important;
  font-size: 1.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sub-card .card-title img {
  height: auto;
  width: 2em;
}

.title {
  line-height: 60px !important;
  margin-bottom: 4px !important;
  font-size: 36px !important;
}

.subtitle {
  color: rgba(255, 255, 255, 0.685) !important;
  font-size: 16px !important;
  font-family: "Roboto", sans-serif;
}

#home-text {
  flex: 1 1 auto;
}

#home-crewmate {
  flex: 1 0 auto;
  min-width: 40%;
  max-width: 40%;
  cursor: default;
}

.stat-card {
  text-align: center;
  font-family: "Poppins";
}

.stat-card .stat-data {
  font-size: 4em;
  font-weight: 700;
  color: #7289da !important;
  line-height: 1;
  min-width: 3em;
}

.stat-label {
  font-size: 1.3em;
  line-height: 1;
  margin-top: 0;
}

div#home-stats {
  display: flex;
  flex-direction: row;
}

.fadeOut {
  opacity: 0;
  width: 0;
  transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}

.fadeIn {
  opacity: 1;
  width: auto;
  transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}




footer {
  padding: 1em;
  margin-top: auto;
}

.muted-snow > canvas {
  opacity: 0.75;
  z-index: -1;
  position: fixed !important;
}
